import { DownloadOutlined, UploadOutlined } from "@ant-design/icons";
import {
  CError,
  CListData,
  CLoadingSpinner,
  CTitle,
  ErrorNotification,
  CFinanceValue,
} from "@components";
import {
  FChange,
  FDownloadTemplate,
  FGetAccess,
  FGetDetailData,
  FGetMoreData,
  FImportData,
  FReadFile,
  FRemoveMaster,
  FSubmitMaster,
} from "@function";
import { useRouter } from "next/router";
import React, { useEffect, useState } from "react";
import CApi from "@api";

const DimentionValue = () => {
  const router = useRouter();
  const { id } = router.query;
  const [total, setTotal] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [loading, setLoading] = useState(false);
  const [isEmpty, setIsEmpty] = useState(true);
  const [isForms, setIsForms] = useState(false);
  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [accessList, setAccessList] = useState({
    viewAny: true,
    view: true,
    create: true,
    update: true,
    delete: true,
  });
  const [paginate, setPaginate] = useState({
    pageSize: 15,
    current_page: 1,
    keyword: "",
  });
  const [forms, setForms] = useState({
    id: "",
    finance_dimension_id: id,
    line: "",
    description: "",
    suspend: "",
    calculate_total_from_multiple_dimension: "",
  });

  function handleSearch(e) {
    setPaginate({
      ...paginate,
      [e.target.id]: e.target.value,
    });
  }
  const onRefresh = () => {
    setPaginate({
      pageSize: 15,
      current_page: 1,
      keyword: "",
    });
    setCurrentPage(1);
    getData();
  };

  useEffect(() => {
    FGetAccess(router, setAccessList, setIsLoading);
  }, []);
  useEffect(() => {
    getData();
  }, [paginate]);
  const getData = () => {
    setLoading(true);
    CApi({
      method: "get",
      url: "api/setup/finance-dimensions/finance-dimensions-value",
      params: {
        q: paginate.keyword,
        finance_dimension_id: id,
      },
    })
      .then((res) => {
        setData(res.data.data);
      })
      .finally(() => {
        setLoading(false);
      });
  };

  const getMoreData = () => {
    FGetMoreData(
      setLoading,
      "api/master/code-activa",
      paginate,
      setData,
      setTotal,
      data,
      currentPage,
      setCurrentPage
    );
  };

  //* Crud
  const handleEdit = (e, item) => {
    e.preventDefault();
    setIsEmpty(false);
    FGetDetailData("api/master/code-activa/" + item, setForms, setIsForms);
  };
  const handleSubmit = () => {
    FSubmitMaster(
      "api/master/code-activa",
      forms,
      setErrors,
      setIsForms,
      setIsEmpty,
      setForms,
      getData
    );
  };
  const handleBack = () => {
    setIsForms(false);
    setIsEmpty(true);
    handleReset();
  };
  const handleInsert = () => {
    setIsForms(false);
    handleReset();
    setTimeout(() => {
      setIsForms(true);
    }, 50);
    setIsEmpty(false);
  };
  const handleReset = () => {
    setForms({
      id: "",
      finance_dimension_id: id,
      line: "",
      description: "",
      suspend: "",
      calculate_total_from_multiple_dimension: "",
    });
  };
  const handleRemove = () => {
    if (forms.id > 0) {
      FRemoveMaster(
        forms.id,
        "api/master/code-activa",
        getData,
        setIsForms,
        setIsEmpty,
        setForms
      );
    } else {
      ErrorNotification("Error", "Please select data...");
    }
  };
  const handleChange = (e) => {
    FChange(e, forms, setForms);
  };
  const handleImport = () => {
    const f = document.getElementById("input-file");
    f.click();
  };
  const handleDownload = async () => {
    FDownloadTemplate(
      "api/master/template/code-activa",
      "Template Master Code Activa.xlsx"
    );
  };
  const onReadFile = (file) => {
    FReadFile(file.target.files[0], dataToImport);
  };
  const dataToImport = (res) => {
    var data = res.map((r) => {
      return { code: r[1] || "", name: r[2] || "", description: r[3] || "" };
    });
    onImportData(data);
  };
  const onImportData = (data) => {
    FImportData(
      "api/master/import/code-activa",
      data,
      getData,
      setLoading,
      "input-file"
    );
  };

  const items = [
    {
      key: "2",
      label: (
        <div onClick={handleImport}>
          <UploadOutlined /> Import Master
        </div>
      ),
    },
    {
      key: "3",
      label: (
        <div onClick={handleDownload}>
          <DownloadOutlined /> Download Template
        </div>
      ),
    },
  ];

  //* End Crud

  if (isLoading) return <CLoadingSpinner />;
  if (!accessList.viewAny) return <CError status={403} />;

  return (
    <>
      <CTitle />
      <CListData
        onDetail={handleEdit}
        handleSearch={handleSearch}
        refresh={onRefresh}
        data={data}
        total={total}
        paginate={paginate}
        loading={loading}
        accessPermission={accessList}
        onLoadMore={getMoreData}
        handleBack={handleBack}
        handleRemove={handleRemove}
        items={items}
        handleInsert={handleInsert}
        isForms={isForms}
        handleSubmit={handleSubmit}
        children={
          <CFinanceValue
            isEmpty={isEmpty}
            forms={forms}
            isForms={isForms}
            handleChange={handleChange}
            errors={errors}
          />
        }
      />
      <input
        type="file"
        style={{ display: "none" }}
        id="input-file"
        onChange={(file) => onReadFile(file)}
        name="file"
        accept=".xlsx, .xls"
      />
    </>
  );
};

export default DimentionValue;
